.wrap{   
      position: absolute;
        font-size: .7em;
        padding:20px ;
        right: 0;
        margin-top: 90px;
        /* background-color: rgba(0,0,0,.1); */
        transition: all 0.4s;
        
        
    }
 
    
    
    
    .widget p{
        display: inline-block;
        line-height: 1em;
    }
    
    .fecha{
        font-family: arial;
        text-align: center;
        /* font-size: 1.5em; */
        font-size: 1.5em;
        /* margin-bottom: 0.3125rem; */
        margin-bottom: 20px;
        background: rgba(0,0,0,.1);
        /* background-color: royalblue; */
      
        width: 100%;

       
        
    }
    
    .reloj{
        font-family: arial;
        width: 100%;
        padding: 0px;
        font-size: 4.5em;
       
        
        background: rgba(0,0,0,.1);
        /* background-color: saddlebrown; */
        display: flex;
        justify-content: flex-end;
        align-items: center;
        /* background-color: brown; */
      
    }
    
    .caja-segundos{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: space-between;
    }   
    
    .segundos, .ampm { 
       font-size: 1.2rem;    
    }
    
    @media(max-width:768px){
        
        .segundos, .ampm { 
            font-size: .8rem;    
         }
         .reloj{
       
            font-size: 2rem; 
        }
        .wrap{
            right: 0;
            background-color: rgba(0,0,0,.1);
            font-size: 15px;
            text-align: end;
        }
        .fecha{
            font-family: arial;
            text-align: center;
            /* font-size: 1.5em; */
            font-size: 1em;
            /* margin-bottom: 0.3125rem; */
            margin-bottom: 15px;
            background: rgba(0,0,0,.1);
            /* background-color: royalblue; */
            padding: 0px;
            width: 100%;
            /* background-color: aqua; */
            
        }
  
        
    }
    @media(min-width:768px){

        
    }