.home_welcome{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0.5);
    height: 100vh;
    font-size: 35px;
    transition: all 0.4s;
    
 
}
.home_welcome h1{
        /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate6 1.2s forwards 0.1s ease-in-out; 
      opacity: 0;
     
  }
  @keyframes animate6{
        
      from{
      transform: translateY(100px);
      }
      
      to{
          opacity: 1;
          transform: translateY(0px);
          
        }
      }

      .home_welcome p{
    /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate5 1.5s forwards .5s ease-in-out; 
      opacity: 0;
     
  }
  @keyframes animate5{
        
      from{
      transform: translateX(100px);
      }
      
      to{
          opacity: 1;
          transform: translateX(0px);
          
        }
      }
      


 .boton_home{
    box-shadow: 0 0 0.7142857142857143rem #08850c;
    /* box-shadow: 0 0 0.7142857142857143rem #0c82c2; */
    padding: 10px ;
    border-radius: 50px;
    width: 200px;
    cursor: pointer;
    /* animation-name: animate6;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-delay: 2s; */
    animation: animate4 1.5s forwards .5s ease-in-out; 
    opacity: 0;
    transition: all .7s;
    
}
@keyframes animate4{
    
    from{
        transform: translateY(100px);
    }
    
    to{
        opacity: 1;
        transform: translateY(0px);
        
    }
}
.boton_home:hover {
    box-shadow: 0 0 0.7142857142857143rem #3c8508;
    /* box-shadow: 0 0 0.7142857142857143rem #0c82c2; */
    /* box-shadow: 0 0 0.7142857142857143rem  rgb(181, 0, 252); */
  /*   box-shadow: 0 0 0.7142857142857143rem #2a8fc5; */
    /* box-shadow: 0 0 0.7142857142857143rem  rgb(252, 0, 244);; */
 /*    color: rgb(252, 0, 244); */
    /* color: rgb(181, 0, 252); */
    width: 240px;
    
    
   
    
}

.welcome_text{
    /* color: rgb(1, 162, 159); */
    /* color: rgb(5, 155, 153); */
  /*   color: rgb(0, 202, 199); */
    color: yellowgreen;
}



@media(max-width:768px){
    .home_welcome{
     font-size: 23px;
        
     
    } 
    .home_welcome p{
        display: none;
        
    } 

    .boton_home{
        margin-top: -20px;
        animation: animate4 1.5s forwards .5s ease-in-out; 
    }
}