.contact{
    background-color: rgba(0,0,0,0.5);
    font-size: 20px;
    max-width: 1366px;
    margin: auto;
    padding: 20px;
}




.contact_title{
    text-align: center;
    padding-top: 150px;
    margin-bottom: 30px;
    font-size: 40px;
    
    
}


  .contact_flex{
      display: flex;
      /* align-items: center; */
      justify-content: center;
      max-width: 1366px;
      margin: auto;
      line-height: 1.8em;
      margin-top: 100px;
      
      
  }
  .contact_left{
      width: 100%;
      
  }
  .contact_right{
  width: 100%;
 
}





/* .contact .container--flex{
  align-items: flex-start;
} */


.contact-information{
  padding:10px;
  padding-top: 0;
  margin-bottom: 30px;

}

.contact-information .column__title{
  font-family: 'Pacifico', cursive;
  font-weight: 100;
}


.contact-information [class*="icon-"]{
  font-size: 1.3em; 
  margin-right: 7px;
  position: relative;
  top: 5px;
}

.contact-information .social-icon{
  justify-content: flex-start;
  margin-bottom: 10px;
}

.contact-information .social-icon [class*="icon-"]{
  background:black;
 
  margin-left: 0;
  margin-right: 15px;
  
}


.formulario__label, .formulario__input-txt, .formulario__textarea{
  display:block;
  width:100%;
}

.formulario__input-txt{
  padding:10px;
  margin-bottom:10px;
  
}

.formulario__input-txt:focus{
  
   border: solid 1px rgb(78, 112, 75);
   
  }
  textarea:focus{
  border: solid 1px rgb(78, 112, 75);

}




.formulario__textarea{
  min-height: 100px;
  min-width: 100%;
  max-width: 100%;
  max-height: 200px;
}

.formulario__btn{
  border:none;
  cursor: pointer;
  font-size: 1em;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
  
}
.formulario__btn{
 box-shadow: 0 0 0.7142857142857143rem #0ece14;
}



@media(max-width:768px){


  .contact_title{
    padding-top: 210px ;
  }
     
    .contact_left{
        margin-bottom: 100px;
    }




    .contact_flex{
  flex-direction: column;
  margin-top: 20px;
        
        
    }
    .contact_title{
      text-align: center;
      
    }
}

input[type="text"],
input[type="email"],
textarea{

    background-color: rgba(0,0,0,0.5);
    color: #fff;
    /* width: 50%; */
    
    box-shadow: 0 0 0.7142857142857143rem #08850c;
  }
 


  
  .contact_right input[type="text"],
  input[type="email"],
  textarea:focus{
    box-shadow: 0 0 0.7142857142857143rem #08850c;
  }

  

  input::placeholder{
    color: yellowgreen; 
  }

  input, textarea{
    border:0;
    outline: none;
  }

  .field{
    border: solid 1px #999;
  }



