.networking li{
    padding: 20px;
    transition: all .7s;
    margin-left: 20px;
    line-height: 1.8em;
    
}
.networking li:hover{
    padding: 20px;
    transform: scale(1.2);
    padding-left: 90px;
}

.networking ul{
    list-style: none;
    
}

.networking{
    padding: 40px 20px;
    font-size: 1.2em;
}

.networking h2{
    margin-top: 20px;
    padding: 20px 20px;
    font-size: 50px ;
    animation: animate5 .5s forwards 0.1s ease-in-out; 
    
}
.networking_list{
    animation: animate6 1.2s forwards 0.1s ease-in-out; 
    
}




@media(max-width:768px){
    .networking{
        padding: 20px 20px;
        font-size: 1.1em;
    }
    .networking li:hover{
        
        transform: scale(1.2);
        padding-left: 30px;
    }
    

}