.projects{
    text-align: center;
    animation: animate9 .5s forwards .5s ease-in-out; 
    opacity: 0;
}

.projects_title{
    margin: auto;
    font-size: 30px;
}

@keyframes animate9{
        
    from{
    transform: translateY(100px);
    }
    
    to{
        opacity: 1;
        transform: translateY(0px);
        
      }
    }




.projects_img img{
   
   width: 250px;
   height: 175px;
   object-fit: cover;
   display: flex;  
   border-radius: 5px;
   margin: 20px;
   transition: all .7s;
}
.projects_img img:hover{
   
  cursor: pointer;
  box-shadow: 0 0 0.7142857142857143rem #08850c;
  transform: scale(1.2);

 }

.projects_img{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    
    
      
}

@media(max-width:768px){
    .projects{
        text-align: center;
    }
}